import { LayoutModule } from './../commons/layout/layout.module';
import { DashboardRoute } from './dashboard.rout.module';
import { IsActivatedGuard } from './../guards/is-activated.guard';
import { MessageService } from './../services/message.service';
import { EventService } from './../services/event.service';
import { WidgetModule } from '../commons/widget/widget.module';
import { RouteService } from './../services/route.service';
import { DashboardService } from './dashboard.service';
import { RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DashboardComponent } from './dashboard.component';
import { AuthenticatedGuard } from '../guards/authenticated.guard';
import { ParseService } from '../services/parse.service';
import { MatCardModule, MatListModule, MatIconModule, MatIconRegistry } from '@angular/material';
import { RedirectStudentGuard } from '../guards/redirect-student.guard';
import { OwlModule } from 'ngx-owl-carousel';
import { ChartsModule } from 'ng2-charts';

@NgModule({
  imports: [
    ChartsModule,
    CommonModule,
    DashboardRoute,
    LayoutModule,
    RouterModule,
    MatCardModule,
    MatListModule,
    MatIconModule,
    OwlModule,
    WidgetModule
  ],
  declarations: [DashboardComponent],
  providers: [
    AuthenticatedGuard,
    IsActivatedGuard,
    DashboardService,
    RouteService,
    EventService,
    MessageService,
    MatIconRegistry,
    RedirectStudentGuard
  ]
})
export class DashboardModule { }



