import { LoadDashBoardAction } from './../message/message-action';
import { ParseService } from './../services/parse.service';
import { IUser, ChartDataset } from './../model/IUser';
import { IMail } from './../model/IMessage';
import { Store } from '@ngrx/store';
import { AppState } from './../message/message-state';
import { MatIconRegistry } from '@angular/material';
import { DomSanitizer } from '@angular/platform-browser';
import { MessageService } from './../services/message.service';
import { Observable } from 'rxjs/Observable';
import { RouteService } from './../services/route.service';
import { Component, OnInit, ViewChild, SimpleChanges } from '@angular/core';
import { EventService } from '../services/event.service';
import { NewsService } from '../services/news.service';
import { LoadingService } from '../services/loading.service';
import { DashboardService } from './dashboard.service';
import * as _ from 'lodash';
import * as moment from 'moment';
import { BaseChartDirective } from 'ng2-charts';

const Parse = require('parse');

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  calendar: any;
  terms = '';
  news: any[] = [];
  messages: IMail[] = [];

  dashData: any = {};

  isSearching = false;
  isLoading = false;
  events$ = this.ds.getEvents();

  currentUser = Parse.User.current();

  studentsCounter$: Observable<number>;
  parentsCounter$: Observable<number>;
  teachersCounter$: Observable<number>;
  adminsCounter$: Observable<number>;
  maleCount$: Observable<number>;
  femaleCount$: Observable<number>;

  inbox$: Observable<IMail[]>;

  chartDataset$: Observable<ChartDataset>;
  registeredStudentChartDataset$: Observable<ChartDataset>;

  localData: ChartDataset = JSON.parse(localStorage.getItem('regitration'));

  local: ChartDataset = this.localData === null ? <ChartDataset>{
    labels: [],
    data: []
  } : this.localData;

  @ViewChild('chartName') myChart: BaseChartDirective;

  options: any = {
    loop: true,
    center: true,
    items: 1,
    margin: 10,
    nav: false,
    autoplay: true,
    autoplayTimeout: 1000 * 10,
    autoplayHoverPause: true,
    autoHeight: true
  };

  constructor(
    private store: Store<AppState>,
    private ps: ParseService,
    private rs: RouteService,
    private ls: LoadingService,
    private ns: NewsService,
    private ds: DashboardService,
    iconRegistry: MatIconRegistry,
    sanitizer: DomSanitizer) {

    iconRegistry.addSvgIcon(
      'envelope',
      sanitizer.bypassSecurityTrustResourceUrl('assets/img/svg/envelope.svg'));
    iconRegistry.addSvgIcon(
      'envelope-o',
      sanitizer.bypassSecurityTrustResourceUrl('assets/img/svg/envelope-o.svg'));
  }

  ngOnInit() {
    this.rs.setBc('sticky-header');
    this.isLoading = true;

    // this.store.dispatch(new LoadDashBoardAction());

    this.studentsCounter$ = this.store.map(this.mapStateToStudentsCount);
    this.parentsCounter$ = this.store.map(this.mapStateToParentsCount);
    this.teachersCounter$ = this.store.map(this.mapStateToTeachersCount);
    this.adminsCounter$ = this.store.map(this.mapStateToAdminCount);

    this.maleCount$ = this.store.skip(1).map(state => this.getSexCount(state, 'male'));
    this.femaleCount$ = this.store.skip(1).map(state => this.getSexCount(state, 'female'));

    this.store
      .map(state => _.values(state.dataState.inbox))
      .flatMap(mails => Observable.from(mails))
      .filter((mail: IMail) => mail.isRead === false)
      .take(5)
      .do(mail => this.messages.push(mail)).subscribe();

    this.store.map(state => _.values(state.dataState.news))
      .flatMap(ns => Observable.from(ns))
      .do(news => this.news.push(news))
      .subscribe();
    this.initChart();
  }

  initChart() {
    const role = this.currentUser.get('role');
    if (role === 'admin' || role === 'teacher') {
      this.chartDataset$ = this.store.select(
        state => {
          let m = 0;
          let f = 0;
          const students: IUser[] = _.values<IUser>(_.cloneDeep(state.dataState.users.students));

          students.map(student => {
            if (student.sex === 'male') {
              m++;
            } else {
              f++;
            }
          });
          return {
            labels: ['Female', 'Male'],
            data: [f, m]
          };
        }
      );
      this.store.select(
        state => {
          const students: IUser[] = _.values<IUser>(_.cloneDeep(state.dataState.users.students));
          const grouped = _.groupBy(students, (user) => moment(user.createdAt).format('MMM'));
          const keys = _.keys(grouped);
          const data = [];

          _.forEach(keys, (key) => {
            data.push(grouped[key].length);
          });
          return {
            labels: keys,
            data: data
          };
        }
      ).skip(1).subscribe(
        data => {
          this.local = data;
          localStorage.setItem('regitration', JSON.stringify(data));
          if (this.myChart) {
            this.myChart.labels = data.labels;
            this.myChart.data = data.data;
            this.myChart.ngOnChanges({});
          }
        }
        );
    }
  }

  getSexCount(state: AppState, sex: string): number {
    let count = 0;
    const students: IUser[] = _.values(_.cloneDeep(state.dataState.users.students));
    return students.reduce((acc, user) => {
        if (user.sex === sex) {
          return count++;
        }
      } , 0);
  }

  mapStateToStudentsCount(state: AppState): number {
    const newState = _.cloneDeep(state);
    return _.values(newState.dataState.users.students).length;
  }

  mapStateToParentsCount(state: AppState): number {
    const newState = _.cloneDeep(state);
    return _.values(newState.dataState.users.parents).length;
  }

  mapStateToTeachersCount(state: AppState): number {
    const newState = _.cloneDeep(state);
    return _.values(newState.dataState.users.teachers).length;
  }

  mapStateToAdminCount(state: AppState): number {
    const newState = _.cloneDeep(state);
    return _.values(newState.dataState.users.admins).length;
  }

  getTitleForStudentsByParents(): String {
    return 'Children';
  }
  getTitleForStudentsByStaff(): String {
    return 'Student';
  }

  getTitle() {
    if (this.currentUser.get('role') === 'admin' || this.currentUser.get('role') === 'teacher') {
      return this.getTitleForStudentsByStaff();
    } else {
      return this.getTitleForStudentsByParents();
    }
  }

  getIcon(message: IMail):String {
    if (message.isRead) {
      return 'envelope-o';
    } else {
      return 'envelope';
    }
  }

  getStudentBySexData() {
    const students = this.dashData.students;
    let m = 0;
    let f = 0;
    students.forEach(student => {
      if (student.get('sex') === 'male') {
        m++;
      } else {
        f++;
      }
    });

    return [f, m];
  }

  getStudentBySexLabels() {
    return ['Female', 'Male'];
  }

}



