import { BasicComponent } from './../commons/layout/basic/basic.component';
import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './dashboard.component';
import { AuthenticatedGuard } from '../guards/authenticated.guard';
import { IsActivatedGuard } from '../guards/is-activated.guard';

export const DashboardRoutes: Routes = [
  {
    path: '',
    component: BasicComponent,
    children: [
      {
        path: '',
        component: DashboardComponent,
        canActivate: [
          AuthenticatedGuard,
          IsActivatedGuard
        ]
      }
    ]
  }
];

export const DashboardRoute: ModuleWithProviders = RouterModule.forChild(DashboardRoutes);



