import { EventService } from './../services/event.service';
import { ParseService } from '../services/parse.service';
import { Observable } from 'rxjs/Observable';
import { DashboardPresenter } from './dashboard.presenter';
import { MessageService } from '../services/message.service';
import { NewsService } from '../services/news.service';

const Parse = require('parse');
const ClassRoom = Parse.Object.extend("ClassRoom");
const News = Parse.Object.extend("News");
const User = Parse.Object.extend(Parse.User);

export class DashboardImpl implements DashboardPresenter {

  currentUser = this.ps.currentUser;

  constructor(private ps: ParseService, private es: EventService, private ms: MessageService, private ns: NewsService) {
  }


  getDashboardData(): Observable<any> {
    if(this.currentUser.get("role") == "admin") {
      return this.getAdminDataObservable();
    } else if (this.currentUser.get("role") == "teacher") {
      return this.getTeacherDataObservable();
    } else if (this.currentUser.get("role") == "parent") {
      return this.getParentDataObservable();
    } else {
      return this.getStudentDataObservable();
    }
  }

  private getAdminDataObservable(): Observable<any> {
    let dashboardData: any = {};
    let school = this.currentUser.get("school");

    return this.getUsers(school, "teacher").flatMap(t => {
      dashboardData.teachers = t;
      // Get parents
      return this.getUsers(school, "parent");
    }).flatMap(p => {
      dashboardData.parents = p;
      // Get students
      return this.getUsers(school, "user");
    })
    .flatMap(s =>{
      dashboardData.students = s;
      return this.ms.getInboxObservable(5, 0);
    })
    .map(m => {
      dashboardData.mails = m;
      return this.es.getEvents();
    }).flatMap(e => {
      dashboardData.events = e;
      return this.ns.getNews();
    }).map(n => {
      dashboardData.news = n;
      return dashboardData;
    });
  }

  private getTeacherDataObservable(): Observable<any> {
    let dashboardData: any = {};
    throw new Error('Method not implemented.');
  }

  private getParentDataObservable(): Observable<any> {
    let dashboardData: any = {};
    return Observable.of(dashboardData);
  }

  private getStudentDataObservable(): Observable<any> {
    let dashboardData: any = {};
    throw new Error('Method not implemented.');
  }

  private getUsers(school:any, role?:string, limit:number = 999, skip:number =0) {
    let query = new Parse.Query(User);
    query.equalTo("school", school);
    query.equalTo("role", role);
    query.limit(limit);
    query.skip(skip);
    query.include(['school', 'profile']);
    return new Observable(observer => {
      query.find().then((u) => {
        observer.next(u);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    })
  }

}



