import { Observable } from 'rxjs/Rx';
import { NewsService } from './../services/news.service';
import { EventService } from './../services/event.service';
import { ParseService } from '../services/parse.service';
import { Injectable } from '@angular/core';
import { DashboardImpl } from './dashboard-impl';
import { MessageService } from '../services/message.service';

@Injectable()
export class DashboardService {

  presenter: DashboardImpl;
  dashData: any = {};
  constructor(private ps: ParseService, private es: EventService, ms: MessageService, ns: NewsService) {
    this.presenter = new DashboardImpl(ps, es, ms, ns);
  }

  public getDashboardData() {
    return this.presenter.getDashboardData();
  }

  getEvents(): Observable<any> {
    return this.es.getEvents();
  }

}



